body {
  margin: 0;
  padding: 0;
}

.header {
  padding: 20px
}

nav {
  position: relative;
  z-index: 1000;
  width: 100%;
  margin: 0;
  top: 0;
  background: #212121;
  color: #ccc;
}

.navbar ul {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar li {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
  -webkit-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  cursor: pointer;
}

.navbar li:hover {
  background: #848484;
  color: #212121;
}

.fixed-nav {
  position: fixed;
}

.content {
  width: 80%;
  margin: 0 auto;
  text-align: justify;
  padding-top: 20px;
}

.content > div {
  margin: 10px 0;
}


